<template>
  <div>
    <div class="little-row3">
      <a
        class="icon-btn is--template-border large-blur"
        :style="codeIsRunning ? 'border-color: #2196F3;' : null"
        @click="onClickToRunCode()"
      >
        <i
          v-if="codeIsRunning"
          class="icon-img is--large fa fa-eye"
          style="color: #2196f3; font-size: large"
        ></i>
        <i v-if="!codeIsRunning" class="icon-img is--large fa fa-eye"></i>
      </a>
      <a
        class="icon-btn is--template-border large-blur"
        @click="onSendCodeToWebflow()"
      >
        <i class="fa fa-upload"></i>
      </a>
    </div>

    <flomotion-comp
      v-if="codeIsRunning"
      v-on:set-actives-again="$emit('set-actives-again')"
      v-on:on-set-again-flomotion-component="onClickToRunCode(true)"
    ></flomotion-comp>

  </div>
</template>

<script>
import engine from "./flomotion";

export default {
    name: "RunComp",
    props: {
        stateList: {
          type: Array,
        },
        desmosUrls: {
          type: Array,
        },
        frameUrl: {
          type: String,
        }
    },
    components: {
      FlomotionComp: () => import('../components/flomotion-comp'),
    },
    data: () => ({
      codeIsRunning: false,
      frameIds: [],
      firstStates: [],
    }),
    methods: {
      onResetFrameIds (frameIds) {
        this.frameIds = frameIds;
      },
      onClickToRunCode(resized) {
        this.codeIsRunning = !this.codeIsRunning;
        this.$emit('on-toggle-run-button', this.codeIsRunning);
        this.onExecuteStateCode();
        if (resized) {
          this.onClickToRunCode()
        }
      },
      onExecuteStateCode() {
        if (this.codeIsRunning) {
          window.scrollTo(0, 0);
          this.onCreateFirstStates();
          const code = engine(
            this.stateList,
            this.frameIds,
            this.firstStates
          );
          this.executeScript(code);
        } else {
          for (const i in this.desmosUrls[0] ? this.desmosUrls: [0]) {
            window[`calculator${+i+1}`].setState(this.firstStates[i]);
          }
          this.firstStates = [];
          window.activeStateId = '';
          const tags = document.getElementsByTagName("script");
          for (let i = tags.length; i >= 0; i--) {
            if (
              tags[i] &&
              tags[i].getAttribute("src") != null &&
              tags[i].getAttribute("src").slice(0, 23) === "data:text/plain;base64,"
            ) {
              tags[i].parentNode.removeChild(tags[i]);
            }
          }
        }
      },
      onCreateFirstStates() {
        this.firstStates = [];
        for (const i in this.desmosUrls[0] ? this.desmosUrls: [0]) {
          const state = window[`calculator${+i+1}`].getState();
          state.expressions.list.forEach((ste) => {
            ste.hidden = true;
            if (ste.latex && ste.latex.slice(1, 6) === "left(") {
              ste.showLabel = false;
            }
          })
          this.firstStates.push(state);
        }
      },
      executeScript(source) {
        const script = document.createElement("script");
        script.onload = script.onerror = function () { this.remove(); };
        script.setAttribute("src", "data:text/plain;base64," + btoa(unescape(encodeURIComponent(source))));
        document.body.appendChild(script);
      },
      copyToClipboard(text) {
        var dummy = document.createElement("textarea");
        document.body.appendChild(dummy);
        dummy.value = text;
        dummy.select();
        document.execCommand("copy");
        document.body.removeChild(dummy);
      },
      async onSendCodeToWebflow (anameId) {
        this.onCreateFirstStates();
        const colors = [];
        for (const i in this.desmosUrls[0] ? this.desmosUrls: [0]) {
          const desmosColors = window[`calculator${+i+1}`].colors;
          colors.push(desmosColors)
        }
        const copy = engine(
          this.stateList,
          this.frameIds,
          this.firstStates,
          colors,
        );
        const updateUrl = this.$route.query.id ? `/${this.$route.query.id}`: '';
        const routeNameID = this.$route.query.nameID ? this.$route.query.nameID : '';
        let nameID = null;
        if (!anameId) nameID = prompt("Please insert your state name", routeNameID.replace(/ /g, "-").replace(/^[^a-z]+|[^\w:.-]+/gi, ""));
        if (nameID != null || anameId) {
          if (!anameId) this.nameID = nameID.replace(/ /g, "-").replace(/^[^a-z]+|[^\w:.-]+/gi, "");
          let loader;
          if (!anameId) loader = this.$loading.show();
          try {
            const newUrl = new URL(`https://x8ki-letl-twmt.n7.xano.io/api:9toeBRNq/states${updateUrl}`)
            let desmosUploadUrls = this.desmosUrls;
            // create desmos for empty link desmos if exists and create empty demos url for that
            if (this.firstStates.length > this.desmosUrls.length && this.desmosUrls.length > 1) {
              desmosUploadUrls.unshift('1')
            }
            const response = await this.$axios({ method: "post",  url: newUrl.pathname, baseURL: newUrl.origin, data: {
                desmos: this.firstStates,
                jsCode: btoa(unescape(encodeURIComponent(copy))),
                desmosID: desmosUploadUrls.join(','),
                frameID: this.frameUrl,
                states: this.stateList,
                nameID: this.nameID || anameId,
              }
            });
            if (response.data.id) {
              const copyCode = [];
              copyCode.push('<scr' + 'ipt' + ' src="https://www.desmos.com/api/v1.6/calculator.js?apiKey=dcb31709b452b1cf9dc26972add0fda6"' + '></scr' + 'ipt>')
              copyCode.push('<scr' + 'ipt>\n' + ` var myHeaders = new Headers(); myHeaders.append("Content-Type", "application/json");
                var requestOptions = { method: 'GET', headers: myHeaders, redirect: 'follow' };
                fetch("https://x8ki-letl-twmt.n7.xano.io/api:9toeBRNq/states/${response.data.id}", requestOptions).then(response => response.json())
                .then((result) => {
                    var script = document.createElement("script");
                    script.onload = script.onerror = function () { this.remove(); };
                    script.setAttribute("src", "data:text/plain;base64," + result.jsCode);
                    document.body.appendChild(script); })
                  .catch(error => console.log('error', error));\n` +'</scr' + 'ipt>')

              await navigator.clipboard.writeText(copyCode.join('\r\n'));
              if (!anameId) alert("code and states has uploaded with id: " + response.data.id + "\n and code has copied to clipnoard");

            } else {
              alert("something is wrong!")
            }
          } catch (error) {
            console.log(error);
            alert(error);
            if (!anameId) loader.hide();
          }
          if (!anameId) loader.hide();
        }
        // const response = await this.$axios.post("/flomotion/add", {
        //   flomotiondesmos: this.firstStates,
        //   animation: btoa(unescape(encodeURIComponent(copy))),
        // });
        // if (response.data.data._id) {
        //   prompt("code and states has uploaded with id: ", response.data.data._id)
        // } else {
        //   alert("something is wrong!")
        // }
      },
    }
}
</script>