
function setTopIndex (name, frameIds) {
    for (let [index, item] of frameIds.entries()) {
        if (item === name) {
            return index
        }
    }
}

function exacName(name) {
    if (name === "Color") {
        return "color";
    } else if (name && name.slice(0, 4) === "SYNC") {
        return "latex";
    } else if (name === "Hidden") {
        return "hidden";
    } else if (name === "Select") {
        return "select";
    }
}


const engine = (stateList, frameIds, firstStates, colors) => {

    const code = [];

    // run in executing code only
    for (const [i, state] of firstStates.entries()) {
        code.push(`
            var state${i+1} = ${JSON.stringify(state)}
            window["calculator${i+1}"].setState(state${i+1})
        `);
    }

    code.push(`function getScrollTop() {
        return window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0; \n
      }`);

    if (colors) {
        for (const [index, item] of frameIds.entries()) {
            code.push(`
                var frameTop${index} = document.getElementById('${item}').offsetTop + 200 \n
                var frameBottom${index} = frameTop${index} + window.screen.height - 200 \n
            `);
        }
    } else {
        for (const [index, item] of frameIds.entries()) {
            code.push(`
                var frameTop${index} = document.getElementById('${item}').offsetTop + 200 \n
                var frameBottom${index} = frameTop${index} + window.screen.height - 200 \n
            `);
        }
    }

    code.push(`
        window.checkIfInView = function () { \n
            if (!window.locked) {
            var viewTop = getScrollTop(); \n
            var viewBottom = viewTop + window.screen.height;
            window.activeStateId = null;
            if (!window.activeDesmosIds) window.activeDesmosIds = [1];
            var activeStateIndex = -1;
            var changingExpIds = [];
    `);

    // if user want to copy (hide all calculators)
    if (colors) {
        code.push(`
        var calcs = document.querySelectorAll('[id^="calculator"]');
        for (var calc of calcs) {
            calc.style.display = 'none'
        }`);
    }

    for (const [index, item] of stateList.entries()) {

        // do state functions
        if (item && item.type === 'inView') {
            code.push(`if (viewBottom > frameTop${setTopIndex(item.startFrame, frameIds) } && frameBottom${setTopIndex(item.startFrame, frameIds) } > viewBottom) {`);
                
            // set active state global variable to detect active state in execute only
            code.push(`
                window.activeStateId = '${item.startFrame}';
                activeStateIndex = ${index};
                changingExpIds = [${stateList[index].transformation.map((t) => {
                    if (/^\d+$/.test(t.compId)) {
                        return t.compId;
                    }
                })}];
                window.activeDesmosIds = [].concat(${
                    stateList[index].transformation[0]
                        ? stateList[index].transformation.map((st) => {
                            return st.desmosId
                          })?.filter((value, index, self) => self.indexOf(value) === index)
                        : 'window.activeDesmosIds'}
                    );
            `);

            code.push(`var progress;`)

            for (const compItem of stateList[index].transformation) {
                
                const itemExp = firstStates[compItem.desmosId-1].expressions.list.find((exp) => exp.id === compItem.compId);

                if ((compItem.attribute === 'SYNC' || compItem.attribute === 'SYNC-Backward') && compItem.compId !== '') {

                    code.push(`progress = (viewBottom - frameTop${setTopIndex(item.startFrame, frameIds) }) /
                    (frameBottom${setTopIndex(item.startFrame, frameIds) } - frameTop${setTopIndex(item.startFrame, frameIds) });`);

                    let { min, max, step } = itemExp.sliderBounds ? itemExp.sliderBounds : itemExp.slider || { min: 0, max: 15, step: .01 };
                    step = itemExp?.sliderBounds?.step || itemExp?.slider?.step;

                    if (compItem.attribute === 'SYNC-Backward') {
                        code.push('progress = Math.abs(1 - progress)');
                    }
 
                    if (isNaN(min) || isNaN(max)) {
                        max ? max = max.replaceAll(String.fromCharCode(92), "").replaceAll("left", "").replace("right", ""): '';
                        min ? min = min.replaceAll(String.fromCharCode(92), "").replaceAll("left", "").replace("right", ""): '';

                        code.push(`var slider = "${min == 0 ? "": min}" + progress + "( ${max ? max : 15} - ${min ? min : 0})"`)

                        code.push(`
                        window["calculator${compItem.desmosId}"].setExpression({
                            id:"${compItem.compId }",
                            ${exacName(compItem.attribute) }: "${itemExp.latex.split('=')[0]}= " + slider,
                            sliderBounds: { min: "${min ? min : 0}", max: "${max ? max : 15}", step: ${step ? step: .01} }
                        })`);
                    } else {
                        code.push(`var slider = ${min == 0 ? "": min} + progress * (${max ? +max : 15} - ${min ? +min : 0})`)
                        
                        code.push(`
                        window["calculator${compItem.desmosId}"].setExpression({
                            id:"${compItem.compId }",
                            ${exacName(compItem.attribute) }: "${itemExp.latex.split('=')[0]}= " + slider,
                            sliderBounds: { min: ${min ? +min : 0}, max: ${max ? +max : 15}, step: ${step ? step: .01} }
                        })`);

                    }
                }

                if ((compItem.attribute === 'SYNC-Custom' || compItem.attribute === 'SYNC-C-Backward') && compItem.compId !== '') {

                    code.push(`progress = (viewBottom - frameTop${setTopIndex(item.startFrame, frameIds) }) /
                    (frameBottom${setTopIndex(item.startFrame, frameIds) } - frameTop${setTopIndex(item.startFrame, frameIds) });`);
                    
                    if (compItem.attribute === 'SYNC-C-Backward') {
                        code.push('progress = Math.abs(1 - progress)');
                    }

                    let { min, max, step } = compItem.sliderBounds ? compItem.sliderBounds : compItem.slider;
                    step = itemExp?.sliderBounds?.step || itemExp?.slider?.step;
                    
                    if (isNaN(min) || isNaN(max)) {
                        max ? max = max.replaceAll(String.fromCharCode(92), "").replaceAll("left", "").replace("right", ""): '';
                        min ? min = min.replaceAll(String.fromCharCode(92), "").replaceAll("left", "").replace("right", ""): '';

                        code.push(`var slider = "${min == 0 ? "": min}" + progress + "( ${max ? max : 15} - ${min ? min : 0})"`)

                        code.push(`
                        window["calculator${compItem.desmosId}"].setExpression({
                            id:"${compItem.compId }",
                            ${exacName(compItem.attribute) }: "${compItem.latex.split('=')[0]}= " + slider,
                            sliderBounds: { min: "${min ? min : 0}", max: "${max ? max : 15}", step: ${step ? step: .01} }
                        })`);
                    } else {
                        code.push(`var slider = ${min == 0 ? "": min} + progress * (${max ? +max : 15} - ${min ? +min : 0})`)
                        
                        code.push(`
                        window["calculator${compItem.desmosId}"].setExpression({
                            id:"${compItem.compId }",
                            ${exacName(compItem.attribute) }: "${compItem.latex.split("=")[0]}= " + slider,
                            sliderBounds: { min: ${min ? +min : 0}, max: ${max ? +max : 15}, step: ${step ? step: .01} }
                        })`);
                    }

                }
                
                if (compItem.attribute === 'SYNC-Constant' && compItem.compId !== '') {
                    
                    let { min, max, step } = compItem.sliderBounds ? compItem.sliderBounds : compItem.slider;
                    step = itemExp?.sliderBounds?.step || itemExp?.slider?.step;

                    code.push(`window["calculator${compItem.desmosId}"].setExpression({
                        id:"${compItem.compId }",
                        ${exacName(compItem.attribute) }: "${compItem.latex}",
                        sliderBounds: { min: ${min ? +min : 0}, max: ${max ? +max : 15}, step: ${step ? step: .01} }
                    })`);

                }

                if (compItem.attribute === 'Hidden' && compItem.compId !== '') {
                    code.push(`window["calculator${compItem.desmosId}"].setExpression({id:"${compItem.compId }", ${exacName(compItem.attribute) }: ${compItem.value}})`);
                }

                if (compItem.attribute === 'Select' && compItem.compId !== '') {
                    if (compItem.value === 'true') {
                        code.push(`window["calculator${compItem.desmosId}"].setExpression({id:"${compItem.compId }", lineOpacity: "1", lineWidth: "6"})`);
                    } else if (compItem.value === 'false') {
                        code.push(`window["calculator${compItem.desmosId}"].setExpression({id:"${compItem.compId }", lineOpacity: "0.5", lineWidth: "2.5"})`);
                    } else if (compItem.value === 'default') {
                        code.push(`window["calculator${compItem.desmosId}"].setExpression({id:"${compItem.compId }", lineOpacity: "0.9", lineWidth: "2.5"})`);
                    }
                }

                if (compItem.attribute === 'Color' && compItem.compId !== '') {
                    code.push(`window["calculator${compItem.desmosId}"].setExpression({id:"${compItem.compId }", ${exacName(compItem.attribute) }: window["calculator${compItem.desmosId}"].colors.${compItem.value}})`);
                }

                if (compItem.attribute === 'Custom' && compItem.compId !== '') {
                    code.push(`window["calculator${compItem.desmosId}"].setExpression({id:"${compItem.compId }", ${compItem.customAttr }: ${compItem.customTrans }})`);
                }

                if (compItem.attribute === 'showLabel' && compItem.compId !== '') {
                    code.push(`window["calculator${compItem.desmosId}"].setExpression({id:"${compItem.compId }", ${compItem.attribute}: ${compItem.value }})`);
                }

                if (compItem.attribute === 'pointActive' && compItem.compId !== '') {
                    code.push(`window["calculator${compItem.desmosId}"].setExpression({id:"${compItem.compId }", showLabel: ${compItem.value }})`);
                    code.push(`window["calculator${compItem.desmosId}"].setExpression({id:"${compItem.compId }", hidden: ${compItem.value === 'true' ? 'false': 'true'}})`);
                }

                if (compItem.attribute === 'Math Boundary') {
                    code.push(`window["calculator${compItem.desmosId}"].setMathBounds({
                        left: ${+compItem.left},
                        right: ${+compItem.right},
                        bottom: ${+compItem.bottom},
                        top: ${+compItem.top}
                    })`);
                }

                if (compItem.attribute === 'xAxisLabel') {
                    code.push(`window["calculator${compItem.desmosId}"].updateSettings({
                        xAxisLabel: "${compItem.xAxisLabel}",
                    })`);
                }

                if (compItem.attribute === 'yAxisLabel') {
                    code.push(`window["calculator${compItem.desmosId}"].updateSettings({
                        yAxisLabel: "${compItem.yAxisLabel}",
                    })`);
                }
            }

            code.push('}')
        }

        if (item && item.type === 'enter') {
            code.push(`if (viewBottom > frameTop${setTopIndex(item.startFrame, frameIds) } && frameBottom${setTopIndex(item.startFrame, frameIds) } > viewTop) {`);

            // set active state global variable to detext active state in execute only 
            code.push(`
                window.activeStateId = '${item.startFrame}';
                activeStateIndex = ${index};
                changingExpIds = [${stateList[index].transformation.map((t) => {
                    if (/^\d+$/.test(t.compId)) {
                        return t.compId;
                    }
                })}];
                window.activeDesmosIds = [].concat(${
                stateList[index].transformation[0]
                    ? stateList[index].transformation.map((st) => {
                        return st.desmosId
                      })?.filter((value, index, self) => self.indexOf(value) === index)
                    : 'window.activeDesmosIds'}
                );
            `)

                for (const compItem of stateList[index].transformation) {

                    if (compItem.attribute === 'Hidden' && compItem.compId !== '') {
                        code.push(`window["calculator${compItem.desmosId}"].setExpression({id:"${compItem.compId }", ${exacName(compItem.attribute) }: ${compItem.value}})`);
                    }

                    if (compItem.attribute === 'Select' && compItem.compId !== '') {
                        if (compItem.value === 'true') {
                            code.push(`window["calculator${compItem.desmosId}"].setExpression({id:"${compItem.compId }", lineOpacity: "1", lineWidth: "6"})`);
                        } else if (compItem.value === 'false') {
                            code.push(`window["calculator${compItem.desmosId}"].setExpression({id:"${compItem.compId }", lineOpacity: "0.5", lineWidth: "2.5"})`);
                        } else if (compItem.value === 'default') {
                            code.push(`window["calculator${compItem.desmosId}"].setExpression({id:"${compItem.compId }", lineOpacity: "0.9", lineWidth: "2.5"})`);
                        }
                    }

                    if (compItem.attribute === 'Color' && compItem.compId !== '') {
                        code.push(`window["calculator${compItem.desmosId}"].setExpression({id:"${compItem.compId }", ${exacName(compItem.attribute) }: window["calculator${compItem.desmosId}"].colors.${compItem.value}})`);
                    }

                    if (compItem.attribute === 'Custom' && compItem.compId !== '') {
                        code.push(`window["calculator${compItem.desmosId}"].setExpression({id:"${compItem.compId }", ${compItem.customAttr }: ${compItem.customTrans }})`);
                    }

                    if (compItem.attribute === 'showLabel' && compItem.compId !== '') {
                        code.push(`window["calculator${compItem.desmosId}"].setExpression({id:"${compItem.compId }", ${compItem.attribute}: ${compItem.value }})`);
                    }

                    if (compItem.attribute === 'pointActive' && compItem.compId !== '') {
                        code.push(`window["calculator${compItem.desmosId}"].setExpression({id:"${compItem.compId }", showLabel: ${compItem.value }})`);
                        code.push(`window["calculator${compItem.desmosId}"].setExpression({id:"${compItem.compId }", hidden: ${compItem.value === 'true' ? 'false': 'true'}})`);
                    }

                    if (compItem.attribute === 'Math Boundary') {
                        code.push(`window["calculator${compItem.desmosId}"].setMathBounds({
                            left: ${+compItem.left},
                            right: ${+compItem.right},
                            bottom: ${+compItem.bottom},
                            top: ${+compItem.top}
                        })`);
                    }

                    if (compItem.attribute === 'xAxisLabel') {
                        code.push(`window["calculator${compItem.desmosId}"].updateSettings({
                            xAxisLabel: "${compItem.xAxisLabel}",
                        })`);
                    }

                    if (compItem.attribute === 'yAxisLabel') {
                        code.push(`window["calculator${compItem.desmosId}"].updateSettings({
                            yAxisLabel: "${compItem.yAxisLabel}",
                        })`);
                    }

                }
            code.push('}');
        }

        // set hidden if components has setted in above enter or exist state
        code.push(` else if (activeStateIndex < ${index} && (frameTop${setTopIndex(item.startFrame, frameIds) } > viewBottom)) {`);

        for (const compItem of stateList[index].transformation) {
            if (
                firstStates[compItem.desmosId-1] &&
                firstStates[compItem.desmosId-1].expressions &&
                firstStates[compItem.desmosId-1].expressions.list[0]
            ) {
                const changedExpressions = firstStates[compItem.desmosId-1].expressions.list.filter((sexp) => {
                    return sexp.id === compItem.compId;
                })
                if (changedExpressions && changedExpressions[0]) {
                    const cexp = changedExpressions[0]
                    if (compItem.attribute === "Hidden") {
                        const value = ({
                            true: false,
                            false: true,
                        })[compItem.value];
                        cexp.hidden = value;
                    }
                    if (compItem.attribute === "showLabel") {
                        const value = ({
                            true: false,
                            false: true,
                        })[compItem.value];
                        cexp.showLabel = value;
                    }
                    if (compItem.attribute === "pointActive") {
                        const value = ({
                            true: false,
                            false: true,
                        })[compItem.value];
                        cexp.showLabel = value;
                        cexp.hidden = !value;
                    }
                    if (compItem.attribute === "Select") {
                        const value = { lineOpacity: cexp.lineOpacity ? cexp.lineOpacity : "0.9", lineWidth: cexp.lineWidth ? cexp.lineWidth: "2.5" }
                        cexp.lineOpacity = value.lineOpacity;
                        cexp.lineWidth = value.lineWidth;
                    }
                    if (compItem.attribute === "Custom") {
                        const value = ({
                            true: false,
                            false: true,
                        })[compItem.customTrans];
                        cexp[compItem.customAttr] = value;
                    }

                    let expExistsInAboveStates = false;
                    for(const [idx, state] of stateList.entries()) {
                        if (idx < index) {
                            for (const trans of state.transformation) {
                                if (trans.compId === compItem.compId && /^\d+$/.test(trans.compId)) {
                                    expExistsInAboveStates = true;
                                }
                            }
                        }
                    }
                    code.push(`
                    if (!changingExpIds.includes(${compItem.compId}) && !${expExistsInAboveStates}) {
                        window["calculator${compItem.desmosId}"].setExpression(${JSON.stringify(cexp)})
                    }`);
                    if (compItem.attribute === 'xAxisLabel') {
                        code.push(`window["calculator${compItem.desmosId}"].updateSettings({
                            xAxisLabel: "${firstStates[compItem.desmosId-1].graph.xAxisLabel}",
                        })`);
                    }
                    if (compItem.attribute === 'yAxisLabel') {
                        code.push(`window["calculator${compItem.desmosId}"].updateSettings({
                            yAxisLabel: "${firstStates[compItem.desmosId-1].graph.yAxisLabel}",
                        })`);
                    }
                    if (compItem.attribute === 'Math Boundary') {
                        code.push(`window["calculator${compItem.desmosId}"].setMathBounds({
                            left: ${firstStates[compItem.desmosId-1].graph.viewport.xmin},
                            right: ${firstStates[compItem.desmosId-1].graph.viewport.xmax},
                            bottom: ${firstStates[compItem.desmosId-1].graph.viewport.ymin},
                            top: ${firstStates[compItem.desmosId-1].graph.viewport.ymax}
                        })`);
                    }
                }
            }
        }
        code.push('}');
 
    }

    // for performance
    // code.push(`
    //     // console.log(newExpressions)
    //     // eroesfsfkslf = ffjkg
    //     var newExpressions1 = newExpressions.filter((a) => a.desmosId === 1);
    //     var newExpressions2 = newExpressions.filter((a) => a.desmosId === 2);
    //     var newExpressions3 = newExpressions.filter((a) => a.desmosId === 3);
    //     if (newExpressions1[0]) {
    //         window.calculator1.setExpressions(newExpressions1);
    //     }
    //     if (newExpressions2[0]) {
    //         window.calculator2.setExpressions(newExpressions2);
    //     }
    //     if (newExpressions3[0]) {
    //         window.calculator3.setExpressions(newExpressions3);
    //     }
    // `);

    // if user want to copy
    if (colors) {
        code.push(`for (var dId of window.activeDesmosIds) {
            document.getElementById('calculator' + dId).style.display = 'block'
        }`);
    }

    // last curly brace of function 
    code.push('}}');

    // if user want to copy
    if (colors) {
        code.shift()
        for (const [i, state] of firstStates.entries()) {
            code.unshift(`
            var state${i+1} = ${JSON.stringify(state)};
            window["calculator${i+1}"].setState(state${i+1})`);
            code.unshift(`
            var element${i+1} = document.getElementById("calculator${i+1}");
            window["calculator${i+1}"] = Desmos.GraphingCalculator(element${i+1}, {
                colors: ${JSON.stringify(colors)},
                border:false,
                invertedColors: true,
                showGrid: false,
                projectorMode: true,
                keypad:false,
                expressionsCollapsed: true,
                settingsMenu:false,lockViewport: true,
                fontSize:16,
                expressions:false
            });
            window["calculator${i+1}"].setState(state${i+1});`);
            code.unshift(`
            var canvas${i+1} = document.getElementsByClassName('canvas');
            var calc${i+1} = document.createElement('div');
            calc${i+1}.id="calculator${i+1}";
            calc${i+1}.classList.add("graph");
            // hide all except 1
            if (${i+1} != 1) calc${i+1}.style.display = 'none';
            canvas${i+1}[0].appendChild(calc${i+1});`);
        }


        code.push(`
            window.addEventListener("scroll", function () {
                window.checkIfInView()
            });
            window.addEventListener("resize", function () {
                window.scrollTo(0, 0);
                // rerender whole script
                var tags = document.getElementsByTagName("script");
                for (var i = tags.length; i >= 0; i--) {
                    if (
                        tags[i] &&
                        tags[i].getAttribute("src") != null &&
                        tags[i].getAttribute("src").slice(0, 23) === "data:text/plain;base64,"
                    ) {
                        // save src of script
                        var src = tags[i].src;
                        // remove it
                        tags[i].parentNode.removeChild(tags[i]);
                        // recreate it
                        var script = document.createElement("script");
                        script.onload = script.onerror = function () { this.remove(); };
                        script.setAttribute("src", src);
                        document.body.appendChild(script);
                    }
                }
            });
        `);
    }

    return code.join('\r\n')

}

export default engine;
